import React from 'react'
import { Container, NavLink } from './Styles'
import { Grid, Typography, Button } from '@material-ui/core'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import { scroller } from 'react-scroll'
import Electrical from '../Components/Electrical'
import Solar from '../Components/Solar'
import ProcessCooling from '../Components/ProcessCooling'
export default function Products() {
    return (
        <Container>
            <Router>




                <Grid container alignItems='center' direction='row'>

                    <Grid item xs={12}>
                        <Typography variant='h2' align='center' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', marginTop: '5rem', fontWeight: '600' }}>
                            Products We Offer
                        </Typography>
                    </Grid>

                    <Grid item xs={12} alignItems='center' >
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', textAlign: 'center', margin: '2rem' }}>
                            <NavLink to='solar'><Link to='/solar'>
                                <Button variant='outlined' style={{ borderColor: '#4B4C5D', color: '#4B4C5D', margin: '2rem' }}>Solar EPC</Button>
                            </Link>
                            </NavLink>

                            <Link to='/electrical'>
                                <Button variant='outlined' style={{ borderColor: '#4B4C5D', color: '#4B4C5D', margin: '2rem' }}>Electrical and Lightning</Button>
                            </Link>


                            <NavLink to='process'><Link to='/process'>
                                <Button variant='outlined' style={{ borderColor: '#4B4C5D', color: '#4B4C5D', margin: '2rem' }}>Process Cooling</Button>
                            </Link>
                            </NavLink>
                        </div>
                    </Grid>

                </Grid>

                <Switch>
                    <Route path='/solar' component={Solar} />
                    <Route path='/electrical' component={Electrical} />
                    <Route path='/process' component={ProcessCooling} />
                </Switch>


            </Router>
        </Container>
    )
}
